.overlay {
    /*position: absolute;*/
    /*z-index: 10;*/
    /*bottom: 50px;*/
    position: absolute;
    bottom: 50px;
    background-color: #008CBA;
    overflow: hidden;
    max-width: 100%;
    max-height: 50%;
    -webkit-transition: .5s ease;
    transition: .5s ease;
}

