.project {
    padding: 1rem;
    text-align: center;

}

.project div {
    position: relative;
    display: inline-block;
    /* if you need ie6/7 support */
    *display: inline;
    zoom: 1;
}

.project p {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    background: #000;
    background: rgba(0, 0, 0, .5);
}

.project-image {
    display: block;
    max-width: 100%;
    height: auto;

}


